body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
    background-color: white;
}
.header-name{
    color: #E01171;
}

@font-face {
    font-family: 'Montserrat';
    src: url(%20//sebasegura97.github.io/marca-personal/static/media/Montserrat-Regular.27e50ffd.otf);
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'Montserrat';
    src: url(%20//sebasegura97.github.io/marca-personal/static/media/Montserrat-Black.6d1796a9.ttf);
    font-weight: 900;
    font-style: normal;
}
/* @font-face {
    font-family: 'Montserrat';
    src: url('../../fonts/Montserrat-ExtraBold.otf');
    font-weight: 800;
    font-style: normal;
} */
a{
  text-decoration: none;
}
h1{
  color: white;
  font-weight: 300;
  font-size: 1.5rem;
  margin: 20px;
}
p{
  color: #4A4A4A;
  font-size: .8rem;
  margin: 0;
}
body {
  background-color: #0F0766;
  margin: auto;
  font-family: 'Montserrat';
}
h2{
  color: white;
  font-weight: 800;
  font-size: 1.5rem;
  margin: 20px;
  text-align: center;
}
h3{
  color: #0F0766;
  font-weight: 300;
  font-size: 1.2rem;
  margin: 20px 0 5px 20px;
}
.app-layout{
  display: grid;
}
.resumen-container{
    margin: 20px;
    display: flex;
    flex-direction: column;

}
.resumen-image{
    width: 100%;
}
.name{
    margin: 0;
    color: #59057B;
}
.article{
    background-color: white;
    padding: 10px;
}
.horizontal-line{
    background-color: #0F0766;
    height: .1rem;
    margin-top: 7px;
    margin-bottom: 10px;
}
.sobremi{
    margin-bottom: 10px;
}
.resumen-item{
    display: flex;
    align-items: center;
    margin-top: 10px;
    /* justify-content: space-between;  */
}
.item-highlight{
    background: #E01171;
    padding: 3px 10px 5px 10px;
    color: white;
}
.item-value{
    margin-left: 10px;
}
.estudios-article{
    background-color: white;
    padding: 0 0 20px 0;
    margin: auto auto 20px auto;
    width: 90%;
}
.horizontal-line-pink{
    background-color: #E01171;
    height: .2rem;
    margin-bottom: 10px;
}
.article-p{
    margin-left: 20px;
}
.title-skills{
    text-align: center;
    margin-left: 0;
    margin-top: 20px;
    margin-right: 0;
    
}
.small-article{
    background-color: white;
    padding: 0 0 20px 0;
    margin: auto auto 20px auto;
}
.skills-image{
    width: 25vw;
}

.images-container{
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 5px;
    margin-left: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.skill-container{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.skills-image-firebase{
    width:13vw;
}
.portfolio-container{
    margin-left: 20px;
    margin-right: 20px;
}
.small-article-portfolio{
    width: 45%;
    margin: 0 0 20px 0;
    padding: 0;
}
.portfolio-flex{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.images-container-portfolio{
    padding: 0;
    margin: 0;
    height: 30vh;
}
.portfolio-item-container{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.portfolio-text{
    margin-top: 10px;
    margin-bottom: 10px;

}
.redes-container{
    flex-direction: column;
}
.redes-article{
    padding: 0;
}
.redes-text{
    margin: 20px 0 20px 0;
}

.mas-article{
    background-size: cover;
    position: relative;
}
.mas-article::after{
    content: "";
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    background-color: rgba(255,255,255,0.7);
}
.mas-text{
    padding: 20px;
    text-align: center;
    position: relative;
    z-index: 2;
}
